@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap);
:root {
  --primary-color: #000;
  --secondary-color: #fff;
}

::selection {
  background-color: #fff;
  background-color: var(--secondary-color);
  color: #000;
  color: var(--primary-color);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  color: #fff;
  color: var(--secondary-color);
  -webkit-tap-highlight-color: transparent;
}

#root {
  height: 100vh;
  background-color: #000;
  background-color: var(--primary-color);
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

/*Main page take all available space*/
#root > :not(:first-child) {
  flex: 1 1;
}

@media (max-width: 700px) {
  #root {
    flex-direction: column-reverse;
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #000;
  background: var(--primary-color);
}

::-webkit-scrollbar-thumb {
  background: #fff;
  background: var(--secondary-color);
}

.account-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.account-title {
  font-size: 3rem;
}

.account-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.account-file-input {
  opacity: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  border-radius: 50%;
}

.account-file-input:hover {
  cursor: pointer;
}

.account-form-error {
  text-align: center;
  color: red;
}

.account-avatar-container {
  position: relative;
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
}

.account-avatar {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.logout-button,
.delete-button {
  text-align: center;
}

.logout-button:hover,
.delete-button:hover {
  text-decoration: underline;
  cursor: pointer;
}

.input-button,
.input-field {
  padding: 1rem;
  font-size: 2rem;
  border-radius: 16px;
  outline: none;
}

.input-field {
  border: 3px solid var(--secondary-color);
  background-color: var(--primary-color);
}

.input-button {
  border: none;
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.input-button:hover {
  cursor: pointer;
}

@media (max-width: 700px) {
  .input-field,
  .input-button {
    font-size: 1rem;
  }
}

.svgIcon-container {
  padding: 1rem;
  border-radius: 50%;

  display: grid;
  place-content: center;
}

.svgIcon-container:hover {
  cursor: pointer;
  background-color: #202020e1;
}

.svg-icon {
  width: 24px;
  height: 24px;
}

.svg-big {
  width: 200px;
  height: 200px;
}

.svg-icon-path {
  fill: var(--secondary-color);
}

.svg-like:hover {
  cursor: pointer;
}

.svg-path-like {
  stroke: var(--secondary-color);
  stroke-width: 3px;
}

.svg-path-liked {
  fill: red;
  stroke: red;
}

.header-list {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  list-style-type: none;
}

.header-title {
  font-size: 4rem;
}

/*Center the Title*/
.header-list li:first-child {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

li > * {
  text-decoration: none;
}

/*Align title left on smaller screen*/
@media (max-width: 700px) {
  .header-list li:first-child {
    left: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

.post-container {
  border: 3px solid var(--secondary-color);
  border-radius: 16px;

  min-height: 300px;
  max-height: 300px;
  min-width: 300px;
  max-width: 300px;

  padding-top: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.post-link {
  text-decoration: none;
}

.post-title {
  padding: 0 1rem;
  border-bottom: 3px solid var(--secondary-color);

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.post-details {
  display: flex;
  align-items: center;
  justify-items: center;
  align-self: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.post-details-numberOfLikes {
  text-align: center;
}

.post-author {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.post-author-details {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.5rem;
}

.addPostButton-container {
  background-color: transparent;
  height: 100px;
  width: 100px;
  border-radius: 50%;

  position: absolute;

  bottom: 20px;
  right: 30px;

  border: 3px solid var(--secondary-color);

  display: grid;
  place-content: center;

  -webkit-backdrop-filter: blur(5px);

          backdrop-filter: blur(5px);
}

.addPostButton-container:hover {
  cursor: pointer;
}

.hide {
  display: none;
}

.addPostModal-background {
  position: fixed;
  top: 0;
  left: 0;

  height: 100%;
  width: 100%;

  background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);

  display: flex;
  align-items: center;
  justify-content: center;
}

.addPostModal-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 3rem;
  border-radius: 16px;
}

.addPostModal-title {
  text-align: center;
}

.addPostModal-form-error {
  text-align: center;
  color: red;
}

.home-container {
  position: relative;
  display: flex;
  padding-top: 3rem;
  overflow: hidden;
}

.home-posts-container {
  flex: 1 1;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  grid-gap: 2rem;
  gap: 2rem;

  overflow: auto;
}

.signUp-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.signUp-title {
  font-size: 3rem;
}

.signUp-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.signUp-form-error {
  text-align: center;
  color: red;
}

.login-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.login-title {
  font-size: 3rem;
}

.login-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.login-form-error {
  text-align: center;
  color: red;
}

.register-button {
  text-align: center;
}

.register-button:hover {
  text-decoration: underline;
  cursor: pointer;
}

.notFound-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  grid-gap: 5rem;

  gap: 5rem;
}

.specificPost-container {
  border: 3px solid var(--secondary-color);
  border-radius: 16px;

  display: flex;
  flex-direction: column;
  margin: 1rem 0;
}

.specificPost-title {
  border-bottom: 3px solid var(--secondary-color);
  padding: 1rem;
}

.specificPost-body {
  padding: 1rem;
  flex: 1 1;
  text-align: center;
}

.specificPost-author-details {
  border-top: 3px solid var(--secondary-color);
  padding: 1rem;
  grid-gap: 1rem;
  gap: 1rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.specificPost-profilePicture {
  height: 100px;
  width: 100px;

  border-radius: 50%;
}

.specificPost-author {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.specificPost-numberOfLikes-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

@media screen and (max-width: 700px) {
  .specificPost-profilePicture {
    height: 50px;
    width: 50px;
  }
}

